export default {
  name: "header.usercenter",
  created() {


    this.listener_login = this.$hub.on('event:login', (data) => {
      this.$nextTick(
          () => {
            this.$forceUpdate();
          }
      );
    });
    this.listener_loginout = this.$hub.on('event:loginout', (data) => {
      this.$nextTick(
          () => {
            this.$forceUpdate();
          }
      );
    });


  },
  destroyed() {
    this.listener_login.off();
    this.listener_loginout.off();
  },
  methods: {
    loginout: function () {
      console.log('loginout click');
      this.$store.commit('loginout');
    },
  }
}
