export default {
  name: "bannerlist",
  data() {
    return {
      ads_list: undefined
    }
  },
  created() {
    setTimeout(() => {
      this.ads_list = [];
      var ads_list = this.$ads_object['top.banner'];
      console.log('top_banner ads_list:', ads_list);
      var ads_keys = Object.keys(ads_list);
      for (var i = 0; i < ads_keys.length; ++i) {
        var ads = ads_list[ads_keys[i]];
        var ads_num = parseInt(ads['ads_num']);
        if(isNaN(ads_num) || !ads_num){
          ads_num = 1;
        }
        console.log('ads:',ads,' ads_num:',ads_num);
        for(let j = 0 ; j < ads_num ; ++j){
          this.ads_list.push(ads);
        }
      }
    }, 300);
  }
}
